import React, { Fragment, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { obtenerUsuariosAction,iniciarSesionAction } from "../actions/usuarioActions";
import Usuario from "./Usuario";
import {CSVLink} from "react-csv"; 
import Cookies from "universal-cookie";
const cookies = new Cookies();
const Usuarios = ({ history }) => {

  const dispatch = useDispatch();

  useEffect(() => {
    if (!cookies.get("id")) {
      history.push("/");
      dispatch(iniciarSesionAction(false,null))
  
      }
    setTimeout(function () {
      cargarUsuarios();
    }, 100);

    //eslint-disable-next-line
  }, []);

  const cargarUsuarios = () => dispatch(obtenerUsuariosAction());
  //obtener el state
  const usuarios = useSelector((state) => state.usuarios.usuarios);
  const error = useSelector((state) => state.usuarios.error);
  const cargando = useSelector((state) => state.usuarios.loading);

  const users = [usuarios.map((usuario) => (
     usuario.nombre
  ))]
  return (
    <Fragment>
      <h2 className="text-center my-5">Listado de Usuarios</h2>
      {error ? (
        <p className="font-weight-bold alert alert-danger text-center mt-4">
          Hubo un error
        </p>
      ) : null}
      {cargando ? <p className="text-center">cargando...</p> : null}
      <table className="table table-striped">
        <thead className="bg-primary table-dark">
          <tr>
            <th scope="col">Nombre</th>
            <th scope="col">Correo</th>
            <th scope="col">Acciones</th>
          </tr>
        </thead>

        {usuarios.length === 0
          ? "no hay usuarios"
          : usuarios.map((usuario) => (
              <Usuario key={usuario._id} usuario={usuario} />
            ))}
      </table>
      <CSVLink
        className="btn btn-primary"
          data={
            users
            }
          
          filename={`usuarios.csv`}
        >
          Descargar listado de usuarios
        </CSVLink>
    </Fragment>
  );
};

export default Usuarios;
