import React from "react";
import { useHistory } from "react-router-dom";
import Swal from "sweetalert2";
import {CSVLink} from "react-csv";
import { useDispatch } from "react-redux";
import { borrarProductoAction,obtenerProductoEditar } from "../actions/usuarioActions";
import Cookies from "universal-cookie";
const cookies = new Cookies();
const Producto = ({ usuario }) => {
  const dispatch = useDispatch();
  const history = useHistory(); //habilitar history para redireccion
  const confirmarProductoEliminar = (id) => {
    //preguntar al usuario
    Swal.fire({
      title: "Estas seguro de eliminar?",
      text: "No se podra recuperar el cliente!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "SI",
      cancelButtonText: "NO"
    }).then((result) => {
      if (result.isConfirmed) {
        //pasarlo al action
        if(cookies.get("id")===id){
          Swal.fire({
            icon: "error",
            title: "Usuario en uso",
          });
        }else{
          dispatch(borrarProductoAction(id));
        }       
        
      }
    });
  };
 
  //Funcion que redirige de forma programada
  const redireccionarEdicion = producto=>{
    dispatch(obtenerProductoEditar(producto))
    history.push(`/usuarios/editar/${producto._id}`)
  }
  const user=[usuario];
  return (
    <tbody>
    <tr>
      <td>{usuario.nombre}</td>
      <td>{usuario.correo}</td>
      <td className="acciones">
        <button
          type="button"
          onClick={()=>redireccionarEdicion(usuario)}
          className="btn btn-primary mr-2"
        >
          Editar
        </button>
        <button
          type="button"
          className="btn btn-danger mr-2"
          onClick={() => confirmarProductoEliminar(usuario._id)}
        >
          Eliminar
        </button>
        <CSVLink
        className="btn btn-primary"
          data={
            user
          } 
          filename={`${usuario.nombre}.csv`}
        >
          Exportar a CSV
        </CSVLink>
      </td>
    </tr>
    </tbody>
  );
};

export default Producto;
