import React, { useState, useEffect } from "react";
//import Productos from "./Productos";
import { editarProductoAction } from "../actions/usuarioActions";
import { useDispatch, useSelector } from "react-redux";
import {useHistory} from 'react-router-dom'
import {UncontrolledAlert } from "reactstrap";
import { iniciarSesionAction } from "../actions/usuarioActions";
import Cookies from "universal-cookie";
const cookies = new Cookies();
const EditarProducto = () => {
  const error = useSelector((state) => state.usuarios.error);
  const history = useHistory();
  //Nuevo state de producto
  const [producto, guardarProducto] = useState({
    nombre: "",
    correo: "",
    id: "",
  });

  //producto a editar
  const productoEditar = useSelector((state) => state.usuarios.productoeditar);
  const dispatch = useDispatch();
  //if (!producto) return <Productos />;
  useEffect(() => {
    if (!cookies.get("id")) {
      history.push("/");
      dispatch(iniciarSesionAction(false, null));

    }
    guardarProducto(productoEditar);
  }, [productoEditar]);

  //Leer los datos del formulario
  const onChangeFormulario = (e) => {
    guardarProducto({
      ...producto,
      [e.target.name]: e.target.value,
    });
    
  };

  const { nombre, correo } = producto;

  const submitEditarProducto = (e) => {
    e.preventDefault();
    dispatch(editarProductoAction(producto));
    //console.log(_id,nombre,precio)
    history.push("/");
  };
  return (
    <div className="row justify-content-center">
      <div className="col-md-8">
        <div className="card">
          <div className="card-body">
            <h2 className="text-center mb-4 font-weight-bold">
              Editar producto
            </h2>
            <form onSubmit={submitEditarProducto}>
              <div className="form-group">
                <label> Nombre</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Nombre"
                  onChange={onChangeFormulario}
                  name="nombre"
                  value={nombre}
                />
              </div>
              <div className="form-group">
                <label> correo</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Correo"
                  onChange={onChangeFormulario}
                  name="correo"
                  value={correo}
                />
              </div>
              <button
                type="submit"
                className="btn btn-primary font-weight-bold text-uppercase d-block w-100"
              >
                Guardar cambios
              </button>
            </form>
            <div className="container text-center mt-5  ">
              {error ? (
                <UncontrolledAlert color="danger">
                  Hubo un error de conexion con el servidor!
                </UncontrolledAlert>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditarProducto;
