import React from "react";
import { Link, useHistory } from "react-router-dom";
import {  useDispatch } from "react-redux";
import { cerrarSesionAction } from "../actions/usuarioActions";

const Header = () => {
  const history = useHistory();
  const dispatch = useDispatch();

  const finalizarSesion = () => {
    
    
    history.push("/");
    
    alert("Redirigiendo");
    dispatch(cerrarSesionAction());
  };

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary justify-content-between">
        <div className="container">
          <h1>
            <Link to={"/usuarios"} className="text-light">
              Proyecto Facultad
            </Link>{" "}
          </h1>
        </div>
        <Link
          to={"/estadisticaEdad"}
          className="btn btn-danger nuevo-post d-block d-md-incline-block mr-3 "
        >
          Ver Estadistica{" "}
        </Link>
        <Link
          to={"/usuarios/nuevo"}
          className="btn btn-danger nuevo-post d-block d-md-incline-block mr-3 "
        >
          Agregar Usuario &#43;
        </Link>
        <button
          onClick={finalizarSesion}
          className="btn btn-danger nuevo-post d-block d-md-incline-block "
        >
          Cerrar Sesion
        </button>
      </nav>
    </div>
  );
};

export default Header;
